enter;\n}\n\n\n// classic clearfix\n.clearfix {\n  clear: both;\n}\n\n\n// Z-levels\n.z-depth-0 {\n  box-shadow: none !important;\n}\n\n/* 2dp elevation modified*/\n.z-depth-1 {\n  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14),\n              0 3px 1px -2px rgba(0,0,0,0.12),\n              0 1px 5px 0 rgba(0,0,0,0.2);\n}\n.z-depth-1-half {\n  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.14), 0 1px 7px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -1px rgba(0, 0, 0, 0.2);\n}\n\n/* 6dp elevation modified*/\n.z-depth-2 {\n  box-shadow: 0 4px 5px 0 rgba(0,0,0,0.14),\n              0 1px 10px 0 rgba(0,0,0,0.12),\n              0 2px 4px -1px rgba(0,0,0,0.3);\n}\n\n/* 12dp elevation modified*/\n.z-depth-3 {\n  box-shadow: 0 8px 17px 2px rgba(0,0,0,0.14),\n              0 3px 14px 2px rgba(0,0,0,0.12),\n              0 5px 5px -3px rgba(0, 0, 0, 0.2);\n}\n\n/* 16dp elevation */\n.z-depth-4 {\n  box-shadow: 0 16px 24px 2px rgba(0,0,0,0.14),\n              0 6px 30px 5px rgba(0,0,0,0.12),\n              0 8px 10px -7px rgba(0,0,0,0.2);\n}\n\n/* 24dp elevation */\n.z-depth-5 {\n  box-shadow: 0 24px 38px 3px rgba(0,0,0,0.14),\n              0 9px 46px 8px rgba(0,0,0,0.12),\n              0 11px 15px -7px rgba(0,0,0,0.2);\n}\n\n.hoverable {\n  transition: box-shadow .25s;\n\n  &:hover {\n    box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);\n  }\n}\n\n// Dividers\n\n.divider {\n  height: 1px;\n  overflow: hidden;\n  background-color: color(\"grey\", \"lighten-2\");\n}\n\n\n//  Blockquote\n\nblockquote {\n  margin: 20px 0;\n  padding-left: 1.5rem;\n  border-left: 5px solid $primary-color;\n}\n\n// Icon Styles\n\ni {\n  line-height: inherit;\n\n  &.left {\n    float: left;\n    margin-right: 15px;\n  }\n  &.right {\n    float: right;\n    margin-left: 15px;\n  }\n  &.tiny {\n    font-size: 1rem;\n  }\n  &.small {\n    font-size: 2rem;\n  }\n  &.medium {\n    font-size: 4rem;\n  }\n  &.large {\n    font-size: 6rem;\n  }\n}\n\n// Images\nimg.responsive-img,\nvideo.responsive-video {\n  max-width: 100%;\n  height: auto;\n}\n\n\n// Pagination\n\n.pagination {\n\n  li {\n    display: inline-block;\n    border-radius: 2px;\n    text-align: center;\n    vertical-align: top;\n    height: 30px;\n\n    a {\n      color: #444;\n      display: inline-block;\n      font-size: 1.2rem;\n      padding: 0 10px;\n      line-height: 30px;\n    }\n\n    &.active a { color: #fff; }\n\n    &.active { background-color: $primary-color; }\n\n    &.disabled a {\n      cursor: default;\n      color: #999;\n    }\n\n    i {\n      font-size: 2rem;\n    }\n  }\n\n\n  li.pages ul li {\n    display: inline-block;\n    float: none;\n  }\n}\n@media #{$medium-and-down} {\n  .pagination {\n    width: 100%;\n\n    li.prev,\n    li.next {\n      width: 10%;\n    }\n\n    li.pages {\n      width: 80%;\n      overflow: hidden;\n      white-space: nowrap;\n    }\n  }\n}\n\n// Breadcrumbs\n.breadcrumb {\n  font-size: 18px;\n  color: rgba(255,255,255, .7);\n\n  i,\n  [class^=\"mdi-\"], [class*=\"mdi-\"],\n  i.material-icons {\n    display: inline-block;\n    float: left;\n    font-size: 24px;\n  }\n\n  &:before {\n    content: '\\E5CC';\n    color: rgba(255,255,255, .7);\n    vertical-align: top;\n    display: inline-block;\n    font-family: 'Material Icons';\n    font-weight: normal;\n    font-style: normal;\n    font-size: 25px;\n    margin: 0 10px 0 8px;\n    -webkit-font-smoothing: antialiased;\n  }\n\n  &:first-child:before {\n    display: none;\n  }\n\n  &:last-child {\n    color: #fff;\n  }\n}\n\n// Parallax\n.parallax-container {\n  position: relative;\n  overflow: hidden;\n  height: 500px;\n\n  .parallax {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    z-index: -1;\n\n    img {\n      opacity: 0;\n      position: absolute;\n      left: 50%;\n      bottom: 0;\n      min-width: 100%;\n      min-height: 100%;\n      transform: translate3d(0,0,0);\n      transform: translateX(-50%);\n    }\n  }\n}\n\n// Pushpin\n.pin-top, .pin-bottom {\n  position: relative;\n}\n.pinned {\n  position: fixed !important;\n}\n\n/*********************\n  Transition Classes\n**********************/\n\nul.staggered-list li {\n  opacity: 0;\n}\n\n.fade-in {\n  opacity: 0;\n  transform-origin: 0 50%;\n}\n\n\n/*********************\n  Media Query Classes\n**********************/\n.hide-on-small-only, .hide-on-small-and-down {\n  @media #{$small-and-down} {\n    display: none !important;\n  }\n}\n.hide-on-med-and-down {\n  @media #{$medium-and-down} {\n    display: none !important;\n  }\n}\n.hide-on-med-and-up {\n  @media #{$medium-and-up} {\n    display: none !important;\n  }\n}\n.hide-on-med-only {\n  @media only screen and (min-width: $small-screen) and (max-width: $medium-screen) {\n    display: none !important;\n  }\n}\n.hide-on-large-only {\n  @media #{$large-and-up} {\n    display: none !important;\n  }\n}\n.hide-on-extra-large-only {\n  @media #{$extra-large-and-up} {\n    display: none !important;\n  }\n}\n.show-on-extra-large {\n  @media #{$extra-large-and-up} {\n    display: block !important;\n  }\n}\n.show-on-large {\n  @media #{$large-and-up} {\n    display: block !important;\n  }\n}\n.show-on-medium {\n  @media only screen and (min-width: $small-screen) and (max-width: $medium-screen) {\n    display: block !important;\n  }\n}\n.show-on-small {\n  @media #{$small-and-down} {\n    display: block !important;\n  }\n}\n.show-on-medium-and-up {\n  @media #{$medium-and-up} {\n    display: block !important;\n  }\n}\n.show-on-medium-and-down {\n  @media #{$medium-and-down} {\n    display: block !important;\n  }\n}\n\n\n// Center text on mobile\n.center-on-small-only {\n  @media #{$small-and-down} {\n    text-align: center;\n  }\n}\n\n// Footer\n.page-footer {\n  padding-top: 20px;\n  color: $footer-font-color;\n  background-color: $footer-bg-color;\n\n  .footer-copyright {\n    overflow: hidden;\n    min-height: 50px;\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    padding: 10px 0px;\n    color: $footer-copyright-font-color;\n    background-color: $footer-copyright-bg-color;\n  }\n}\n\n// Tables\ntable, th, td {\n   border: none;\n}\n\ntable {\n  width:100%;\n  display: table;\n  border-collapse: collapse;\n  border-spacing: 0;\n\n  &.striped {\n    tr {\n      border-bottom: none;\n    }\n\n    > tbody {\n      > tr:nth-child(odd) {\n        background-color: $table-striped-color;\n      }\n\n      > tr > td {\n        border-radius: 0;\n      }\n    }\n  }\n\n  &.highlight > tbody > tr {\n    transition: background-color .25s ease;\n    &:hover {\n      background-color: $table-striped-color;\n    }\n  }\n\n  &.centered {\n    thead tr th, tbody tr td {\n      text-align: center;\n    }\n  }\n}\n\ntr {\n  border-bottom: 1px solid $table-border-color;\n}\n\ntd, th{\n  padding: 15px 5px;\n  display: table-cell;\n  text-align: left;\n  vertical-align: middle;\n  border-radius: 2px;\n}\n\n// Responsive Table\n@media #{$medium-and-down} {\n\n  table.responsive-table {\n    width: 100%;\n    border-collapse: collapse;\n    border-spacing: 0;\n    display: block;\n    position: relative;\n\n    td:empty:before {\n      content: '\\00a0';\n    }\n\n    th,\n    td {\n      margin: 0;\n      vertical-align: top;\n    }\n\n    th { text-align: left; }\n    thead {\n      display: block;\n      float: left;\n\n      tr {\n        display: block;\n        padding: 0 10px 0 0;\n\n        th::before {\n          content: \"\\00a0\";\n        }\n      }\n    }\n    tbody {\n      display: block;\n      width: auto;\n      position: relative;\n      overflow-x: auto;\n      white-space: nowrap;\n\n      tr {\n        display: inline-block;\n        vertical-align: top;\n      }\n    }\n    th {\n      display: block;\n      text-align: right;\n    }\n    td {\n      display: block;\n      min-height: 1.25em;\n      text-align: left;\n    }\n    tr {\n      border-bottom: none;\n      padding: 0 10px;\n    }\n\n    /* sort out borders */\n    thead {\n      border: 0;\n      border-right: 1px solid $table-border-color;\n    }\n  }\n\n}\n\n\n// Collections\n.collection {\n  margin: $element-top-margin 0 $element-bottom-margin 0;\n  border: 1px solid $collection-border-color;\n  border-radius: 2px;\n  overflow: hidden;\n  position: relative;\n\n  .collection-item {\n    background-color: $collection-bg-color;\n    line-height: $collection-line-height;\n    padding: 10px 20px;\n    margin: 0;\n    border-bottom: 1px solid $collection-border-color;\n\n    // Avatar Collection\n    &.avatar {\n      min-height: 84px;\n      padding-left: 72px;\n      position: relative;\n\n      // Don't style circles inside preloader classes.\n      &:not(.circle-clipper) > .circle,\n      :not(.circle-clipper) > .circle {\n        position: absolute;\n        width: 42px;\n        height: 42px;\n        overflow: hidden;\n        left: 15px;\n        display: inline-block;\n        vertical-align: middle;\n      }\n      i.circle {\n        font-size: 18px;\n        line-height: 42px;\n        color: #fff;\n        background-color: #999;\n        text-align: center;\n      }\n\n\n      .title {\n        font-size: 16px;\n      }\n\n      p {\n        margin: 0;\n      }\n\n      .secondary-content {\n        position: absolute;\n        top: 16px;\n        right: 16px;\n      }\n\n    }\n\n\n    &:last-child {\n      border-bottom: none;\n    }\n\n    &.active {\n      background-color: $collection-active-bg-color;\n      color: $collection-active-color;\n\n      .secondary-content {\n        color: #fff;\n      }\n    }\n  }\n  a.collection-item{\n    display: block;\n    transition: .25s;\n    color: $collection-link-color;\n    &:not(.active) {\n      &:hover {\n        background-color: $collection-hover-bg-color;\n      }\n    }\n  }\n\n  &.with-header {\n    .collection-header {\n      background-color: $collection-bg-color;\n      border-bottom: 1px solid $collection-border-color;\n      padding: 10px 20px;\n    }\n    .collection-item {\n      padding-left: 30px;\n    }\n    .collection-item.avatar {\n      padding-left: 72px;\n    }\n  }\n\n}\n// Made less specific to allow easier overriding\n.secondary-content {\n  float: right;\n  color: $secondary-color;\n}\n.collapsible .collection {\n  margin: 0;\n  border: none;\n}\n\n\n\n// Responsive Videos\n.video-container {\n    position: relative;\n    padding-bottom: 56.25%;\n    height: 0;\n    overflow: hidden;\n\n    iframe, object, embed {\n      position: absolute;\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: 100%;\n    }\n}\n\n// Progress Bar\n.progress {\n    position: relative;\n    height: 4px;\n    display: block;\n    width: 100%;\n    background-color: lighten($progress-bar-color, 40%);\n    border-radius: 2px;\n    margin: $element-top-margin 0 $element-bottom-margin 0;\n    overflow: hidden;\n  .determinate {\n    position: absolute;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    background-color: $progress-bar-color;\n    transition: width .3s linear;\n  }\n  .indeterminate {\n    background-color: $progress-bar-color;\n    &:before {\n      content: '';\n      position: absolute;\n      background-color: inherit;\n      top: 0;\n      left:0;\n      bottom: 0;\n      will-change: left, right;\n      // Custom bezier\n      animation: indeterminate 2.1s cubic-bezier(0.650, 0.815, 0.735, 0.395) infinite;\n\n    }\n    &:after {\n      content: '';\n      position: absolute;\n      background-color: inherit;\n      top: 0;\n      left:0;\n      bottom: 0;\n      will-change: left, right;\n      // Custom bezier\n      animation: indeterminate-short 2.1s cubic-bezier(0.165, 0.840, 0.440, 1.000) infinite;\n      animation-delay: 1.15s;\n    }\n  }\n}\n@keyframes indeterminate {\n    0% {\n      left: -35%;\n      right:100%;\n    }\n    60% {\n      left: 100%;\n      right: -90%;\n    }\n    100% {\n      left: 100%;\n      right: -90%;\n    }\n}\n\n@keyframes indeterminate-short {\n    0% {\n      left: -200%;\n      right: 100%;\n    }\n    60% {\n      left: 107%;\n      right: -8%;\n    }\n    100% {\n      left: 107%;\n      right: -8%;\n    }\n}\n\n\n/*******************\n  Utility Classes\n*******************/\n\n.hide {\n  display: none !important;\n}\n\n// Text Align\n.left-align {\n  text-align: left;\n}\n.right-align {\n  text-align: right\n}\n.center, .center-align {\n  text-align: center;\n}\n\n.left {\n  float: left !important;\n}\n.right {\n  float: right !important;\n}\n\n// No Text Select\n.no-select {\n  user-select: none;\n}\n\n.circle {\n  border-radius: 50%;\n}\n\n.center-block {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n}\n\n.truncate {\n  display: block;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n.no-padding {\n  padding: 0 !important;\n}\n","// ==========================================================================\n// Materialize variables\n// ==========================================================================\n//\n// Table of Contents:\n//\n//  1. Colors\n//  2. Badges\n//  3. Buttons\n//  4. Cards\n//  5. Carousel\n//  6. Collapsible\n//  7. Chips\n//  8. Date + Time Picker\n//  9. Dropdown\n//  10. Forms\n//  11. Global\n//  12. Grid\n//  13. Navigation Bar\n//  14. Side Navigation\n//  15. Photo Slider\n//  16. Spinners | Loaders\n//  17. Tabs\n//  18. Tables\n//  19. Toasts\n//  20. Typography\n//  21. Footer\n//  22. Flow Text\n//  23. Collections\n//  24. Progress Bar\n\n\n\n// 1. Colors\n// ==========================================================================\n\n$primary-color: color(\"materialize-red\", \"lighten-2\") !default;\n$primary-color-light: lighten($primary-color, 15%) !default;\n$primary-color-dark: darken($primary-color, 15%) !default;\n\n$secondary-color: color(\"teal\", \"lighten-1\") !default;\n$success-color: color(\"green\", \"base\") !default;\n$error-color: color(\"red\", \"base\") !default;\n$link-color: color(\"light-blue\", \"darken-1\") !default;\n\n\n// 2. Badges\n// ==========================================================================\n\n$badge-bg-color: $secondary-color !default;\n$badge-height: 22px !default;\n\n\n// 3. Buttons\n// ==========================================================================\n\n// Shared styles\n$button-border: none !default;\n$button-background-focus: lighten($secondary-color, 4%) !default;\n$button-font-size: 14px !default;\n$button-icon-font-size: 1.3rem !default;\n$button-height: 36px !default;\n$button-padding: 0 16px !default;\n$button-radius: 2px !default;\n\n// Disabled styles\n$button-disabled-background: #DFDFDF !default;\n$button-disabled-color: #9F9F9F !default;\n\n// Raised buttons\n$button-raised-background: $secondary-color !default;\n$button-raised-background-hover: lighten($button-raised-background, 5%) !default;\n$button-raised-color: #fff !default;\n\n// Large buttons\n$button-large-font-size: 15px !default;\n$button-large-icon-font-size: 1.6rem !default;\n$button-large-height: $button-height * 1.5 !default;\n$button-floating-large-size: 56px !default;\n\n// Small buttons\n$button-small-font-size: 13px !default;\n$button-small-icon-font-size: 1.2rem !default;\n$button-small-height: $button-height * .9 !default;\n$button-floating-small-size: $button-height * .9 !default;\n\n// Flat buttons\n$button-flat-color: #343434 !default;\n$button-flat-disabled-color: lighten(#999, 10%) !default;\n\n// Floating buttons\n$button-floating-background: $secondary-color !default;\n$button-floating-background-hover: $button-floating-background !default;\n$button-floating-color: #fff !default;\n$button-floating-size: 40px !default;\n$button-floating-radius: 50% !default;\n\n\n// 4. Cards\n// ==========================================================================\n\n$card-padding: 24px !default;\n$card-bg-color: #fff !default;\n$card-link-color: color(\"orange\", \"accent-2\") !default;\n$card-link-color-light: lighten($card-link-color, 20%) !default;\n\n\n// 5. Carousel\n// ==========================================================================\n\n$carousel-height: 400px !default;\n$carousel-item-height: $carousel-height / 2 !default;\n$carousel-item-width: $carousel-item-height !default;\n\n\n// 6. Collapsible\n// ==========================================================================\n\n$collapsible-height: 3rem !default;\n$collapsible-line-height: $collapsible-height !default;\n$collapsible-header-color: #fff !default;\n$collapsible-border-color: #ddd !default;\n\n\n// 7. Chips\n// ==========================================================================\n\n$chip-bg-color: #e4e4e4 !default;\n$chip-border-color: #9e9e9e !default;\n$chip-selected-color: #26a69a !default;\n$chip-margin: 5px !default;\n\n\n// 8. Date + Time Picker\n// ==========================================================================\n\n$datepicker-display-font-size: 2.8rem;\n$datepicker-calendar-header-color: #999;\n$datepicker-weekday-color: rgba(0, 0, 0, .87) !default;\n$datepicker-weekday-bg: darken($secondary-color, 7%) !default;\n$datepicker-date-bg: $secondary-color !default;\n$datepicker-year: rgba(255, 255, 255, .7) !default;\n$datepicker-focus: rgba(0,0,0, .05) !default;\n$datepicker-selected: $secondary-color !default;\n$datepicker-selected-outfocus: desaturate(lighten($secondary-color, 35%), 15%) !default;\n$datepicker-day-focus: transparentize(desaturate($secondary-color, 5%), .75) !default;\n$datepicker-disabled-day-color: rgba(0, 0, 0, .3) !default;\n\n$timepicker-clock-color: rgba(0, 0, 0, .87) !default;\n$timepicker-clock-plate-bg: #eee !default;\n\n\n// 9. Dropdown\n// ==========================================================================\n\n$dropdown-bg-color: #fff !default;\n$dropdown-hover-bg-color: #eee !default;\n$dropdown-color: $secondary-color !default;\n$dropdown-item-height: 50px !default;\n\n\n// 10. Forms\n// ==========================================================================\n\n// Text Inputs + Textarea\n$input-height: 3rem !default;\n$input-border-color: color(\"grey\", \"base\") !default;\n$input-border: 1px solid $input-border-color !default;\n$input-background: #fff !default;\n$input-error-color: $error-color !default;\n$input-success-color: $success-color !default;\n$input-focus-color: $secondary-color !default;\n$input-font-size: 16px !default;\n$input-margin-bottom: 8px;\n$input-margin: 0 0 $input-margin-bottom 0 !default;\n$input-padding: 0 !default;\n$label-font-size: .8rem !default;\n$input-disabled-color: rgba(0,0,0, .42) !default;\n$input-disabled-solid-color: #949494 !default;\n$input-disabled-border: 1px dotted $input-disabled-color !default;\n$input-invalid-border: 1px solid $input-error-color !default;\n$input-icon-size: 2rem;\n$placeholder-text-color: lighten($input-border-color, 20%) !default;\n\n// Radio Buttons\n$radio-fill-color: $secondary-color !default;\n$radio-empty-color: #5a5a5a !default;\n$radio-border: 2px solid $radio-fill-color !default;\n\n// Range\n$range-height: 14px !default;\n$range-width: 14px !default;\n$track-height: 3px !default;\n\n// Select\n$select-border: 1px solid #f2f2f2 !default;\n$select-background: rgba(255, 255, 255, 0.90) !default;\n$select-focus: 1px solid lighten($secondary-color, 47%) !default;\n$select-option-hover: rgba(0,0,0,.08) !default;\n$select-option-focus: rgba(0,0,0,.08) !default;\n$select-option-selected: rgba(0,0,0,.03) !default;\n$select-padding: 5px !default;\n$select-radius: 2px !default;\n$select-disabled-color: rgba(0,0,0,.3) !default;\n\n// Switches\n$switch-bg-color: $secondary-color !default;\n$switch-checked-lever-bg: desaturate(lighten($switch-bg-color, 25%), 25%) !default;\n$switch-unchecked-bg: #F1F1F1 !default;\n$switch-unchecked-lever-bg: rgba(0,0,0,.38) !default;\n$switch-radius: 15px !default;\n\n\n// 11. Global\n// ==========================================================================\n\n// Media Query Ranges\n$small-screen-up: 601px !default;\n$medium-screen-up: 993px !default;\n$large-screen-up: 1201px !default;\n$small-screen: 600px !default;\n$medium-screen: 992px !default;\n$large-screen: 1200px !default;\n\n$medium-and-up: \"only screen and (min-width : #{$small-screen-up})\" !default;\n$large-and-up: \"only screen and (min-width : #{$medium-screen-up})\" !default;\n$extra-large-and-up: \"only screen and (min-width : #{$large-screen-up})\" !default;\n$small-and-down: \"only screen and (max-width : #{$small-screen})\" !default;\n$medium-and-down: \"only screen and (max-width : #{$medium-screen})\" !default;\n$medium-only: \"only screen and (min-width : #{$small-screen-up}) and (max-width : #{$medium-screen})\" !default;\n\n\n// 12. Grid\n// ==========================================================================\n\n$num-cols: 12 !default;\n$gutter-width: 1.5rem !default;\n$element-top-margin: $gutter-width/3 !default;\n$element-bottom-margin: ($gutter-width*2)/3 !default;\n\n\n// 13. Navigation Bar\n// ==========================================================================\n\n$navbar-height: 64px !default;\n$navbar-line-height: $navbar-height !default;\n$navbar-height-mobile: 56px !default;\n$navbar-line-height-mobile: $navbar-height-mobile !default;\n$navbar-font-size: 1rem !default;\n$navbar-font-color: #fff !default;\n$navbar-brand-font-size: 2.1rem !default;\n\n// 14. Side Navigation\n// ==========================================================================\n\n$sidenav-width: 300px !default;\n$sidenav-font-size: 14px !default;\n$sidenav-font-color: rgba(0,0,0,.87) !default;\n$sidenav-bg-color: #fff !default;\n$sidenav-padding: 16px !default;\n$sidenav-item-height: 48px !default;\n$sidenav-line-height: $sidenav-item-height !default;\n\n\n// 15. Photo Slider\n// ==========================================================================\n\n$slider-bg-color: color('grey', 'base') !default;\n$slider-bg-color-light: color('grey', 'lighten-2') !default;\n$slider-indicator-color: color('green', 'base') !default;\n\n\n