d (min-width : 360 + ($i * $interval-size)) {\n      font-size: 1.2rem * (1 + (.02 * $i));\n    }\n    $i: $i + 1;\n  }\n\n  // Handle below 360px screen\n  @media only screen and (max-width: 360px) {\n    font-size: 1.2rem;\n  }\n}\n","// Scale transition\r\n.scale-transition {\r\n  &.scale-out {\r\n    transform: scale(0);\r\n    transition: transform .2s !important;\r\n  }\r\n\r\n  &.scale-in {\r\n    transform: scale(1);\r\n  }\r\n\r\n  transition: transform .3s cubic-bezier(0.53, 0.01, 0.36, 1.63) !important;\r\n}","\n\n.card-panel {\n  transition: box-shadow .25s;\n  padding: $card-padding;\n  margin: $element-top-margin 0 $element-bottom-margin 0;\n  border-radius: 2px;\n  @extend .z-depth-1;\n  background-color: $card-bg-color;\n}\n\n.card {\n  position: relative;\n  margin: $element-top-margin 0 $element-bottom-margin 0;\n  background-color: $card-bg-color;\n  transition: box-shadow .25s;\n  border-radius: 2px;\n  @extend .z-depth-1;\n\n\n  .card-title {\n    font-size: 24px;\n    font-weight: 300;\n    &.activator {\n      cursor: pointer;\n    }\n  }\n\n  // Card Sizes\n  &.small, &.medium, &.large {\n    position: relative;\n\n    .card-image {\n      max-height: 60%;\n      overflow: hidden;\n    }\n    .card-image + .card-content {\n      max-height: 40%;\n    }\n    .card-content {\n      max-height: 100%;\n      overflow: hidden;\n    }\n    .card-action {\n      position: absolute;\n      bottom: 0;\n      left: 0;\n      right: 0;\n    }\n  }\n\n  &.small {\n    height: 300px;\n  }\n\n  &.medium {\n    height: 400px;\n  }\n\n  &.large {\n    height: 500px;\n  }\n\n  // Horizontal Cards\n  &.horizontal {\n    &.small, &.medium, &.large {\n      .card-image {\n        height: 100%;\n        max-height: none;\n        overflow: visible;\n\n        img {\n          height: 100%;\n        }\n      }\n    }\n\n    display: flex;\n\n    .card-image {\n      max-width: 50%;\n      img {\n        border-radius: 2px 0 0 2px;\n        max-width: 100%;\n      