\n      box-shadow: none;\n    }\n\n    .todo-button {\n      transition: all 0.3s ease;\n    }\n  }\n  .select {\n    margin: 1rem;\n  }\n  .filter-todo {\n    height: 3.6rem;\n  }\n}\n\n.todoItems-container {\n  display: flex;\n\n  .todo-list {\n    min-width: 30%;\n    list-style: none;\n  }\n\n  .todo {\n    margin: 0.5rem;\n    font-size: 1.5rem;\n    color: black;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    transition: all 1s ease;\n  }\n\n  .todo-item {\n    flex: 1;\n    padding: 0.7rem 0.5rem;\n    background: #ffffff;\n    height: 3.6rem;\n    margin-right: 0.3;\n    border-radius: 0.3rem;\n  }\n\n  .trash-btn,\n  .complete-btn {\n    color: white;\n    border: none;\n    border-radius: 0.3rem;\n    margin: 0 0.3rem;\n    cursor: pointer;\n    font-size: 1rem;\n    transition: 0.3s ease-in-out;\n  }\n\n  .complete-btn {\n    background: rgb(11, 212, 162);\n    &:hover {\n      color: rgb(13, 199, 13);\n      background: rgb(30, 250, 195);\n    }\n  }\n\n  .trash-btn {\n    background: #ff6f47;\n    &:hover {\n      background: #ffffff;\n      color: crimson;\n    }\n  }\n\n  .fa-trash,\n  .fa-check {\n    pointer-events: none;\n  }\n}\n\n.fall {\n  transform: translateY(10rem) rotateZ(20deg);\n  opacity: 0;\n}\n\n.completed {\n  text-decoration: line-through;\n  opacity: 0.5;\n}\n\n.todo-back {\n  position: absolute;\n  z-index: 10;\n  display: flex;\n  justify-content: center;\n  width: 36px;\n  margin: 10px;\n}\n\n@media (max-width: 800px) {\n  .todoItems-container {\n    .todo-list {\n      .todo {\n        font-size: 1.2rem;\n      }\n    }\n  }\n}\n\n@media (max-width: 500px) {\n  .todo-header h2 {\n    font-size: 2rem;\n  }\n  .todo-form {\n    width: 90%;\n    .input-field {\n      .todo-input {\n        font-size: 1.2rem;\n      }\n      .btn {\n        .material-icons {\n          font-size: 1.6rem;\n        }\n      }\n    }\n  }\n  .todoItems-container {\n    .todo-list {\n      .todo {\n        font-size: 1rem;\n        .btn {\n          .material-icons {\n            font-size: 1.6rem;\n          }\n        }\n      }\n    }\n  }\n}\n","// === Variable ===\n$black-color: #000000;\n// === /Variable ===\n\n// === Common ===\n\n:root {\n  font-size: 10px;\n}\n\n// === /Common ===\n\n// === Currency ===\n.currency {\n  display: flex;\n  flex-direction: column;\n  font-size: 2rem;\n  width: 100%;\n  color: $black-color;\n\n  .currency-title {\n    text-align: center;\n    padding: 10px;\n  }\n  .currency-body {\n    overflow: hidden;\n    .currency-container {\n      display: flex;\n      justify-content: space-around;\n      align-items: center;\n      padding: 7px 5px;\n      padding-top: 7px;\n      cursor: pointer;\n      &:hover {\n        background-color: #4f4f4f;\n      }\n\n      .currency-info {\n        display: flex;\n        align-items: center;\n\n        &__flag {\n          width: 50px;\n        }\n\n        &__name {\n          margin-left: 10px;\n        }\n      }\n\n      .currency-rate {\n      }\n    }\n  }\n}\n\n@media (max-width: 850px) {\n  .currency {\n    font-size: 1.8rem;\n    .currency-body {\n      .currency-container {\n        .currency-info {\n          &__flag {\n            width: 30px;\n          }\n        }\n      }\n    }\n  }\n}\n\n@media (max-width: 700px) {\n  .currency {\n    font-size: 1.5rem;\n    .currency-body {\n      .currency-container {\n        .currency-info {\n          &__flag {\n            width: 20px;\n          }\n        }\n      }\n    }\n  }\n}\n\n@media (max-width: 576px) {\n  .currency {\n    font-size: 1.2rem;\n    .currency-body {\n      .currency-container {\n        .currency-info {\n          &__flag {\n            display: none;\n            width: 20px;\n          }\n          &__name {\n            margin-left: 0;\n          }\n        }\n      }\n    }\n  }\n}\n\n// === /Currency ===\n","// === Variable ===\n$black-color: #000000;\n// === /Variable ===\n\n// === Common ===\n\n:root {\n  font-size: 10px;\n}\n\n// === /Common ===\n\n.clock {\n  display: flex;\n  justify-content: space-around;\n  align-items: center;\n  flex-wrap: wrap;\n  // background: #091921;\n  padding: 5px;\n}\n\n.clock-container {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\n.clock-body {\n  width: 80px;\n  height: 80px;\n  display: flex;\n  margin: 10px 0;\n  justify-content: center;\n  align-items: center;\n  background: url(../assets/images/clock.png) #091921 no-repeat;\n  background-size: cover;\n  border: 4px solid #091921;\n  border-radius: 100%;\n  box-shadow: 0 -15px 15px rgba(255, 255, 255, 0.05), inset 0 -15px 15px rgba(255, 255, 255, 0.05),\n    0 15px 15px rgba(0, 0, 0, 0.3), inset 0 15px 15px rgba(0, 0, 0, 0.3);\n\n  &::before {\n    content: \"\";\n    position: absolute;\n    width: 5px;\n    height: 5px;\n    background: #fff;\n    border-radius: 50%;\n    z-index: 10000;\n  }\n\n  .hour,\n  .min,\n  .sec {\n    position: absolute;\n  }\n\n  .hr,\n  .mn,\n  .sc {\n    display: flex;\n    justify-content: center;\n    border-radius: 50%;\n  }\n\n  .hr {\n    width: 40px;\n    height: 40px;\n\n    &::before {\n      content: \"\";\n      position: absolute;\n      width: 3px;\n      height: 20px;\n      background: #ffffff;\n      z-index: 10;\n      border-radius: 6px 6px 0 0;\n    }\n  }\n  .mn {\n    width: 50px;\n    height: 50px;\n\n    &::before {\n      content: \"\";\n      position: absolute;\n      width: 3px;\n      height: 25px;\n      background: #ffffff;\n      z-index: 11;\n      border-radius: 6px 6px 0 0;\n    }\n  }\n  .sc {\n    width: 60px;\n    height: 60px;\n\n    &::before {\n      content: \"\";\n      position: absolute;\n      width: 1px;\n      height: 40px;\n      background: #ff105e;\n      z-index: 12;\n      border-radius: 6px 6px 0 0;\n    }\n  }\n}\n\n.time {\n  font-size: 1.5rem;\n  color: $black-color;\n}\n\n.clock-title {\n  font-size: 1.8rem;\n  font-weight: bold;\n  color: $black-color;\n}\n\n[data-clock-night=\"false\"] {\n  background: url(../assets/images/clock.png) #ffffff no-repeat;\n  background-size: cover;\n\n  .hr {\n    &::before {\n      background: $black-color;\n    }\n  }\n  .mn {\n    &::before {\n      background: $black-color;\n    }\n  }\n}\n\n@media (max-width: 900px) {\n  .clock {\n    flex-direction: column;\n  }\n}\n","// === Variables ===;\n$color_blue1: #0260e8;\n$color_blue2: #51eaff;\n$color_blue3: #453eac;\n$color_mint: #76fec5;\n$color_green1: #41b619;\n$color_green2: #00dfc8;\n$color_green3: #70e852;\n$color_yellow: #ffc11e;\n$color_peach1: #f85c50;\n$color_peach2: #fe9e76;\n$color_peach3: #ffa96b;\n$color_orange: #df8600;\n$color_pink: #ff2970;\n$color_red: #b40707;\n$color_purple: #ef2fa2;\n\n// === /Variables ===;\n\n#map {\n  width: 100%;\n  height: 100%;\n}\n\n#marker {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  position: relative;\n  font-size: 18px;\n  text-align: center;\n  border-radius: 100%;\n  height: 32px;\n  width: 32px;\n  cursor: pointer;\n}\n.marker-icon {\n  text-align: center;\n  line-height: 100px;\n}\n\n.iw-container {\n  font-size: 15px;\n\n  .iw-title {\n    font-size: 22px;\n    font-weight: bold;\n    text-align: center;\n    padding: 10px;\n  }\n\n  .iw-content {\n    max-height: 200px;\n    overflow-y: auto;\n    overflow-x: hidden;\n\n    .iw-img {\n      float: left;\n      border-radius: 20px;\n      margin: 10px;\n    }\n\n    .iw-info {\n      cursor: pointer;\n    }\n  }\n\n  .iw-contacts {\n    font-size: 16px;\n    font-weight: bold;\n    margin-top: 7px;\n\n    .iw-address {\n    }\n\n    .iw-link {\n      color: black;\n      margin-top: 5px;\n      text-decoration: none;\n      transition: 0.2s ease-in-out;\n\n      &:hover {\n        color: #ffab00;\n      }\n    }\n  }\n  .iw-button {\n    display: block;\n    float: right;\n    padding: 3px;\n    cursor: pointer;\n    border: none;\n    border-radius: 4px;\n    background-color: blue;\n    color: white;\n    margin: